import React from 'react'
import Timer from './Timer/Timer'
import './Task.css'

const Task = ({ taskData, onSelectVariant, selectedVariantId }) => {
  const { time, code, question } = taskData
  const variants = taskData.variants.map((variant, ind) => {
    const id = ind + 1
    let classes = ["variant"]
    if (selectedVariantId === id) {
      classes.push("selected")
    }
    return (
      <li
        className={classes.join(' ')}
        onClick={() => onSelectVariant(id)}
        key={variant}
        id={id}>
        {variant}
      </li>
    )
  })

  return (
    <div className="task">
      <Timer onTimeUp={() => onSelectVariant(-1)} elementKey={taskData.question} time={time || 20} />
      <div className="question-wrapper">
        <p className="question">{question}</p>
        {code ? <div className="code-example"><pre><code>{code}</code></pre></div> : null}
      </div>
        <ul className="variants-list">
        {variants}
      </ul>
    </div>
  )
}

export default Task