import React from 'react'
import Preloader from '../UI/Preloader/Preloader'
import Task from '../Task/Task'
import StartTestingScreen from '../Start-testing-screen/Start-testing-screen'
import ResultsScreen from '../Results-screen/Results-screen'
import TotalCount from '../Total-count/Total-count'

const ActiveQuiz = ({ activeQuizInfo, onSelectVariant, onStartTesting }) => {
  const { description,
    stage,
    tasksSheet,
    answers,
    activeTaskID,
    selectedVariantID,
    isFetchingResult,
    result } = activeQuizInfo
  let content = null

  switch (stage) {
    case 'start':
      content = <StartTestingScreen onStartTesting={onStartTesting} description={description} />
      break;
    case 'finish':
      content = (isFetchingResult ? <Preloader /> : <ResultsScreen result={result} />)
      break;
    case 'testing':
      if (tasksSheet.length !== 0) {
        content = (
          <>
            <Task
              taskData={tasksSheet[activeTaskID]}
              onSelectVariant={onSelectVariant}
              selectedVariantId={selectedVariantID}
            />
            <TotalCount current={answers.length + 1} total={tasksSheet.length} />
          </>
        )
      }
      break;
    default:
      content = (<div>No data here</div>)
  }

  return (
    <>
      {content}
    </>
  )
}

export default ActiveQuiz