export const addAnswer = (answer) => ({ type: 'ADD_ANSWER', payload: answer })
export const changeTask = () => ({ type: 'CHANGE_TASK' })
export const setStage = (stage) => ({ type: 'SET_STAGE', payload: stage })
export const setActiveQuizInfo = (info) => ({ type: 'SET_ACTIVE_QUIZ_INFO', payload: info })
export const setResult = (result) => ({ type: 'SET_RESULT', payload: result })
export const setIsFetchingQuiz = (value) => ({ type: 'SET_IS_FETCHING_QUIZ', payload: value })
export const setIsFetchingResult = (value) => ({ type: 'SET_IS_FETCHING_RESULT', payload: value })

export const getActiveQuizInfo = (quizID, isFromSearch) => {
  return (dispatch, getState, { getFirestore }) => {
    const firestore = getFirestore()
    const collection = isFromSearch ? "privateQuizes" : "quizes"

    dispatch(setIsFetchingQuiz(true))
    firestore.collection(collection).doc(quizID).get()
      .then(doc => {
        if (doc.exists) {
          const data = doc.data()
          dispatch(setActiveQuizInfo({ ...data, quizID }))
          dispatch(setIsFetchingQuiz(false))
        } else {
          console.log("No such document!")
        }
      })
      .catch(error => {
        console.log("Error getting document:", error)
      })
  }
}

const tryToSaveResultOnServer = (result, quizID) => {
  return (dispatch, getState, { getFirestore, getFirebase }) => {
    const firestore = getFirestore()
    const { uid } = getState().firebase.auth

    if (!uid) return

    firestore.collection('users').doc(uid).get()
      .then(doc => doc.data())
      .then(data => {
        const dataObj = data.stats[quizID]
        const field1 = `stats.${quizID}.bestResult`
        const field2 = `stats.${quizID}.times`

        if (dataObj) {
          firestore.collection('users').doc(uid).update({
            [field1]: dataObj.bestResult > result ? dataObj.bestResult : result,
            [field2]: dataObj.times + 1
          })
        } else {
          firestore.collection('users').doc(uid).update({
            [field1]: result,
            [field2]: 1
          })
        }
      })
      .catch(error => console.log(error))
  }
}

export const getResult = (tasksSheet, userAnswers, quizID) => {
  return (dispatch) => {
    dispatch(setIsFetchingResult(true))

    const PERCENT = 100
    const correctAnswers = tasksSheet.map(el => ({ answerID: Number(el.correct) }))
    let resultAnswers = 0

    userAnswers.forEach((el, ind) => {
      if (Number(el.answerID) === correctAnswers[ind].answerID) {
        resultAnswers += 1
      }
    })

    const result = Math.round((resultAnswers * PERCENT) / correctAnswers.length)

    dispatch(tryToSaveResultOnServer(result, quizID))
    dispatch(setResult(result))
    dispatch(setIsFetchingResult(false))
  }
}
