import React from 'react'
import ActiveQuiz from '../components/Active-quiz/Active-quiz'
import Preloader from '../components/UI/Preloader/Preloader'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import {
  setStage, addAnswer, getActiveQuizInfo,
  getResult, changeTask
} from '../reducers/action-creators/active-quiz-info-AC'

class ActiveQuizContainer extends React.Component {
  componentDidMount() {
    const quizID = this.props.match.params.quizID
    const isFromSearch = this.props.location.state?.fromSearch || false

    this.props.getActiveQuizInfo(quizID, isFromSearch)
    this.props.setStage('start')
  }

  componentWillUnmount() {
    this.props.setStage(null)
  }

  onStartTesting = () => {
    this.props.setStage('testing')
  }

  onSelectVariant = (id) => {
    const { tasksSheet, activeTaskID, selectedVariantID } = this.props.activeQuizInfo
    if (selectedVariantID !== null) {
      return false
    }

    const newAnswer = {
      taskID: activeTaskID,
      answerID: id,
    }

    this.props.addAnswer(newAnswer)
    const timeoutID = setTimeout(() => {
      clearTimeout(timeoutID)
      if (activeTaskID === tasksSheet.length - 1) {
        this.finishQuiz()
      } else {
        this.props.changeTask()
      }
    }, 1000)
  }

  finishQuiz = () => {
    const { answers, tasksSheet, quizID } = this.props.activeQuizInfo
    this.props.getResult(tasksSheet, answers, quizID)
    this.props.setStage('finish')
  }

  render() {
    return (
      <>
        {this.props.activeQuizInfo.isFetchingQuizInfo
          ? <Preloader />
          : (<ActiveQuiz
            {...this.props}
            onSelectVariant={this.onSelectVariant}
            onStartTesting={this.onStartTesting} />)}
      </>
    )
  }
}

const mapStateToProps = ({ activeQuizInfo }) => {
  return {
    activeQuizInfo,
  }
}

const mapDispatchToProps = {
  getActiveQuizInfo,
  setStage,
  addAnswer,
  changeTask,
  getResult,
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ActiveQuizContainer))