import React, { createRef } from 'react'

import './Timer.css'

const MAX_X_DELTA = 105
const MS_IN_SECOND = 1000

class Timer extends React.Component {
  constructor (props) {
    super(props)
    this.timerRef = createRef()
    this.requestId = null
  }

  componentDidMount() {
    this.startAnimation()
  }

  componentDidUpdate(prevProps) {
    if (this.props.elementKey !== prevProps.elementKey) {
      this.startAnimation()
    } 
  }
  
  componentWillUnmount() {
    window.cancelAnimationFrame(this.requestId)
  }

  calculateXDelta = (timeDelta) => {
    return timeDelta / (this.props.time * MS_IN_SECOND) * MAX_X_DELTA
  }

  startAnimation = () => {
    if (this.requestId) {
      window.cancelAnimationFrame(this.requestId)
    }
    const element = this.timerRef.current
    let prevTimestamp = null
    let xDelta = 0

    element.style.transform = ''

    const step = (timestamp) => {
      if (!prevTimestamp) {
        prevTimestamp = timestamp
      }
      const timeDelta = timestamp - prevTimestamp
      prevTimestamp = timestamp
      xDelta += this.calculateXDelta(timeDelta)

      element.style.transform = `translateX(-${xDelta}%)`;
      if (xDelta >= MAX_X_DELTA) {
        this.finishAnimation()
      } else {
        this.requestId = window.requestAnimationFrame(step);
      }
    }
    this.requestId = window.requestAnimationFrame(step);
  }

  finishAnimation = () => {
    window.cancelAnimationFrame(this.requestId)
    this.props.onTimeUp()
  }

  render () {
    return (
      <div className="timer-wrapper mb-3">
        <div ref={this.timerRef}></div>
      </div>
    )
  }
}

export default Timer