import React from 'react'
import { Button, Image } from 'react-bootstrap'
import { CHAPTERS } from '../../constants/constants'

import './Start-testing-screen.css'

const StartTestingScreen = ({ onStartTesting, description }) => {
  const includedTopics = description.includedTopics && description.includedTopics.map(el => {
    return (
      <li key={el}>
        {el}
      </li>
    )
  })
  const imgTitle = CHAPTERS[description.chapter]
  
  return (
    <div>
      <div className="quiz-image mb-3">
        <Image src={require(`../../assets/img/${imgTitle}-bg.jpg`)} />
      </div>
      <h2 className="text-center mb-3 quiz-chapter">{description.chapter}</h2>
      <p className="text-center mb-3 quiz-title">{description.title}</p>
      <ul className="included-topics mb-3">
        {includedTopics}
      </ul>
      <div className="text-center">
        <Button
          className="btn btn-primary btn-lg custom-bg mb-5"
          onClick={onStartTesting}>
          Начать тест
        </Button>
      </div>
    </div>
  )
}

export default StartTestingScreen